
"use client";

import { useEffect, useState, Suspense } from "react"; // Added useState
import { useRouter, useSearchParams } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { useAuth } from "@/hooks/use-auth"; 
import { useToast } from "@/hooks/use-toast"; // Added useToast
import { ROUTES, APP_NAME } from "@/lib/constants";
import { MailCheck, Loader2, AlertCircle } from "lucide-react";
import { applyActionCode } from "firebase/auth"; 
import { auth } from "@/lib/firebase";

function VerifyEmailContent() {
  const { user, firebaseUser, sendVerificationEmail, isLoading, logout, updateSession } = useAuth();
  const router = useRouter();
  const searchParams = useSearchParams();
  const { toast } = useToast(); // Initialize toast
  const [verificationMessage, setVerificationMessage] = useState<string | null>(null);
  const [isVerifyingCode, setIsVerifyingCode] = useState(false);

  useEffect(() => {
    const mode = searchParams.get('mode');
    const actionCode = searchParams.get('oobCode');

    if (mode === 'verifyEmail' && actionCode) {
      setIsVerifyingCode(true);
      applyActionCode(auth, actionCode)
        .then(async () => { // Make async to await updateSession
          setVerificationMessage("Email Anda berhasil diverifikasi! Anda akan diarahkan.");
          toast({title: "Verifikasi Berhasil", description: "Email Anda telah diverifikasi. Silakan login jika diperlukan."});
          await updateSession(); // Refresh user state
          // The main useEffect below will handle redirection based on updated user state
        })
        .catch((error) => {
          console.error("Firebase email verification error:", error);
          setVerificationMessage(`Gagal memverifikasi email: ${error.message}`);
          toast({title: "Verifikasi Gagal", description: error.message, variant: "destructive"});
        })
        .finally(() => {
          setIsVerifyingCode(false);
        });
    }
  }, [searchParams, router, logout, toast, updateSession]);


  useEffect(() => {
    if (!isLoading) {
      if (!user) { 
        if (!searchParams.get('oobCode')) { // Only redirect if not currently handling an oobCode
          router.replace(ROUTES.LOGIN);
        }
      } else if (user.isVerified) {
        // User is verified, redirect to their dashboard
        switch (user.role) {
          case 'admin': router.replace(ROUTES.ADMIN_DASHBOARD); break;
          case 'guru': router.replace(ROUTES.GURU_DASHBOARD); break;
          case 'siswa': router.replace(ROUTES.SISWA_DASHBOARD); break;
          case 'pimpinan': router.replace(ROUTES.PIMPINAN_DASHBOARD); break;
          case 'superadmin': router.replace(ROUTES.ADMIN_DASHBOARD); break;
          default: router.replace(ROUTES.HOME);
        }
      }
      // If user exists but not verified, and no oobCode is being processed, stay on this page.
    }
  }, [user, isLoading, router, searchParams]);

  const handleResendVerification = async () => {
    if (firebaseUser && !firebaseUser.emailVerified) { // Check firebaseUser directly
      await sendVerificationEmail();
    }
  };

  if (isLoading || isVerifyingCode) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        {isVerifyingCode && <p className="ml-3">Memverifikasi email Anda...</p>}
        {isLoading && !isVerifyingCode && <p className="ml-3">Memuat status autentikasi...</p>}
      </div>
    );
  }
  
  // If user is loaded, verified, and not in the process of oobCode verification, they should have been redirected.
  // If they land here without an oobCode and are somehow not verified, show the "please verify" message.
  if (!user && !searchParams.get('oobCode')) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <p>Sesi tidak ditemukan atau telah berakhir. Mengarahkan ke Halaman Login...</p>
        {/* Redirect should happen via useEffect, but this is a fallback display */}
      </div>
    );
  }
  
  // User exists but not verified, and not actively verifying an oobCode
  if (user && !user.isVerified && !searchParams.get('oobCode')) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-background p-4">
        <Card className="w-full max-w-md shadow-xl">
          <CardHeader className="text-center">
            <div className="mx-auto flex h-16 w-16 items-center justify-center rounded-full bg-primary/10 text-primary mb-4">
              <MailCheck className="h-8 w-8" />
            </div>
            <CardTitle className="text-3xl font-headline text-primary">{APP_NAME}</CardTitle>
            <CardDescription className="text-muted-foreground">
              Verifikasi Email Anda
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6 text-center">
            <p>
              Halo {user.email || "Pengguna"}, email Anda belum terverifikasi.
            </p>
            <p>
              Silakan periksa kotak masuk email Anda untuk tautan verifikasi.
              Jika Anda tidak menerimanya, Anda dapat meminta email baru.
            </p>
            <Button onClick={handleResendVerification} className="w-full" disabled={isLoading}>
              {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : "Kirim Ulang Email Verifikasi"}
            </Button>
            <Button variant="link" onClick={logout} className="text-sm text-muted-foreground">
              Kembali ke Halaman Login
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  // Displaying verification message if oobCode was processed
  if (verificationMessage) {
     return (
      <div className="flex min-h-screen items-center justify-center bg-background p-4">
        <Card className="w-full max-w-md shadow-xl">
          <CardHeader className="text-center">
            <div className="mx-auto flex h-16 w-16 items-center justify-center rounded-full bg-primary/10 text-primary mb-4">
              {verificationMessage.includes("berhasil") ? <MailCheck className="h-8 w-8 text-green-500" /> : <AlertCircle className="h-8 w-8 text-destructive" />}
            </div>
            <CardTitle className="text-3xl font-headline text-primary">{APP_NAME}</CardTitle>
            <CardDescription className="text-muted-foreground">
              Status Verifikasi
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6 text-center">
            <p className={verificationMessage.includes("berhasil") ? "text-green-600" : "text-destructive"}>
                {verificationMessage}
            </p>
            {verificationMessage.includes("berhasil") && !isLoading && (
                <p>Anda akan diarahkan...</p>
            )}
            <Button variant="link" onClick={logout} className="text-sm text-muted-foreground">
                Ke Halaman Login
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  // Fallback for unexpected states, though useEffects should handle most redirections.
  return (
    <div className="flex min-h-screen items-center justify-center">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3">Mengarahkan...</p>
    </div>
  );
}

export default function VerifyEmailPage() {
  return (
    <Suspense fallback={<div className="flex min-h-screen items-center justify-center"><Loader2 className="h-12 w-12 animate-spin text-primary" /></div>}>
      <VerifyEmailContent />
    </Suspense>
  )
}
